body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline;
}

.App {
  display: flex;
  align-items: center;
  height: 100vh;
  justify-content: center;
  background: linear-gradient(to bottom right, #3D4258, #6670A0, #9688B4);
  font-family: 'Nunito', sans-serif;
}

blockquote {
  font-size: 2.5rem;
  margin: 0.8em 0.8em 0.8em 0;
  opacity: 0.8;
}


#quote-container {
  display: flex;
  height: auto;
  max-width: 60vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(to bottom right, white, #F8F8F8);
  box-shadow: 5px 15px 20px -15px rgba(0, 0, 0, 0.3), 0 35px 50px -25px rgba(0, 0, 0, 0.3), 0 85px 60px -25px rgba(0, 0, 0, 0.1);
}
#new-quote {
    border: 1px solid transparent;
    border-radius: 40px;
    text-decoration: none;
    padding: .5rem 1rem;
    margin: 1em;
    color: rgba(255, 255, 255, .7);
    font-weight: 700;
    background: rgba(10, 20, 30, .1);
    font-size: 2rem;
    outline: none;
}
#new-quote:hover {
    background: rgba(10, 20, 30, .3);
}
#new-quote:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

#buttons {
  display: flex;
  flex-direction: row;
  background-color: ;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #EDECF0;
}
#tweet-quote {
  margin: 1.4em;
  padding-left: 0.6em ;
  outline: none;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width: 768px) {
  #quote-container {
  min-width: 95vw;
}
blockquote {
  font-size: 1.5rem;
}
#new-quote {
  font-size: 1.5rem;
}
}

